@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes slideDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
.contact {
  background: #111;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;
}
.contact .avatarWrapper {
  position: relative;
  margin: auto;
  margin-top: -310px;
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact .avatar {
  display: block;
  height: 280px;
  width: 280px;
  border-radius: 140px;
  object-fit: cover;
  background: #7f8c8d;
  box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.5);
}
.contact .avatar.loaded {
  animation-name: fadeIn;
  animation-duration: 0.2s;
}
.contact .genaiButton {
  position: absolute;
  top: 0;
  right: 0;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.8);
  border-radius: 20px;
  padding: 10px 20px;
  cursor: pointer;
  font-family: 'Open Sans';
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
  transition: all 0.3s;
  animation: blinkBorder 2s infinite;
}
.contact .genaiButton:hover {
  background: linear-gradient(135deg, #764ba2 0%, #667eea 100%);
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.6);
}
@keyframes blinkBorder {
  0%,
  100% {
    border-color: rgba(255, 255, 255, 0.8);
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
  }
  50% {
    border-color: #ffffff;
    box-shadow: 0 4px 20px rgba(102, 126, 234, 0.8), 0 0 30px rgba(102, 126, 234, 0.4);
  }
}
.contact .social a:hover {
  border-bottom: 1px solid white;
}
.contact .title {
  font-family: 'Open Sans';
  font-size: 32px;
  font-weight: 800;
  color: #dddddd;
  margin: 0;
}
.contact .subtitle {
  font-family: 'Source Sans Pro';
  font-size: 20px;
  font-weight: 300;
  margin-top: 17px;
  margin-bottom: 25px;
  color: #eeeeee;
  user-select: none;
}
.contact .subtitle .location {
  display: inline-block;
  font-family: 'Open Sans';
  font-weight: 800;
  font-size: 50%;
  color: #dddddd;
  vertical-align: top;
  transform: rotate(-10deg) translateY(-10px);
}
.contact .subtitle .location .striked {
  text-decoration: line-through;
  text-decoration-thickness: 3px;
  text-decoration-color: #a03535;
}
.contact .subtitle .location.invisible {
  color: #111;
}
.contact .title,
.contact .subtitle,
.contact .social {
  animation-name: slideDown;
  animation-duration: 0.2s;
}
.contact .social {
  animation-name: slideUp;
}
@media (max-height: 890px) {
  .contact .avatarWrapper {
    margin-top: -210px;
  }
  .contact .avatar {
    height: 180px;
    width: 180px;
    border-radius: 90px;
  }
  .contact .genaiButton {
    font-size: 12px;
    padding: 8px 16px;
  }
}
@media (max-height: 590px) {
  .contact .avatarWrapper {
    margin-top: -170px;
  }
  .contact .avatar {
    height: 140px;
    width: 140px;
    border-radius: 70px;
  }
  .contact .genaiButton {
    font-size: 10px;
    padding: 6px 12px;
  }
}
